<template>
	<div class="initialized">
		<div class="container" id='home'>
			<h4></h4>
			<div class="nav">
				<ul>
					<li><a href="#/index"></a></li>
					<li class="active"><a href="#"></a></li>
					<li><a href="#/publishMes"></a></li>
					<li><a href="#/permission"></a></li>
				</ul>
			</div>
			<div class="cur-location">
				<a href="#/index">[]</a> - <a href="#/list">[]</a>
			</div>
			<div class="content-list-item">
				<ul>
					<li v-for='i in users'>						
						<router-link v-bind:to="'/listDetail/'+i.urlControl">
							<div class="list-item">
								<span class="item-words">&gt;&gt;{{i.topic}}</span>
								<span class="publish-info">{{i.name}} {{i.time}}</span>
							</div>
						</router-link>
					</li>
				</ul>
				<div class="page-tab">
					<ul>
						<li><a href="#" @click='prevPage'></a></li>
						<li><a href="#" @click='prevPage'></a></li>
						<li><a href="#" @click='nextPage'></a></li>
						<li><a href="#"></a></li>
						<li><a href="#"></a></li>
						<li><a href="#"></a></li>
					</ul>
				</div>
			</div>
		</div>
		<div class="footer">
			<p>&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
		</div>
	</div>
</template>



<script>
	export default {
		name:'list',
		data:function() {
			return {
				userStatus:false,
				users:[],
				usersCache:[],
				curUser:[]
			}
		},
		mounted(){
			this.$nextTick(function(){
				this.initData()
			})
		},
		methods:{
			initData(){
				this.$http.get('../../static/userInfo.json').then((res)=>{
					this.usersCache=res.body.users;
					this.users=this.usersCache.slice(0,6);
					var storage = window.localStorage;
					var pubMesTopic = storage.getItem('pubMesTopic'),
						pubMesContent = storage.getItem('pubMesContent'),
						curName = storage.getItem('name'),
						curTime = storage.getItem('time')
					if(pubMesTopic){
						this.users.unshift({
							topic:pubMesTopic,
							name:curName,
							time:curTime
						})
						this.users.pop()
					}
				})
			},
			nextPage(){
				this.users = this.usersCache.slice(6,12);
			},
			prevPage(){
				this.users = this.usersCache.slice(0,6);
			}
	    }
	}
</script>

<style>
	@import '../css/main.css';
	.list-item{
		height: 40px;
	}
	.item-words{
		display: inline-block;
		font-style: normal;
		width: 1000px;
		height: 40px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		color: #111;
	}
</style>


