<template>
	<div class="initialized">
		<div class="container" id='home'>
			<div class="userinfos" v-show='userStatus'>
			</div>
			<h4></h4>
			<div class="cur_user_info" v-if='userStatus'>{{curUser[0].name}}</div>
			<div class="nav">
				<ul>
					<li><a href="#/index"></a></li>
					<li><a href="#/list"></a></li>
					<li class="active"><a href="#"></a></li>
					<li><a href="#/permission"></a></li>
				</ul>
			</div>
			<div class="cur-location">
				<a href="#/index">[]</a> - <a href="#">[]</a>
			</div>
			<!--  -->
			<div class="loginbox" v-show="loginStatus" @click="loginboxClick">
		        <div class="box" @click="stopProp()">
		            <h3></h3>
		            <form name="login" id="login">
		                <input type="text" placeholder="" class="username" :class='{hasFocus:judgeFocus == 1}' @click='judgeFocus = 1' @blur='judgeFocus = 0'>
		                <input type="password" placeholder="" class="psw" :class='{hasFocus:judgeFocus == 2}' @click='judgeFocus = 2'  @blur='judgeFocus = 0'>
		                <input type="button" value="" @click="login">
		            </form>
		        </div>
	        </div>
	        <!--  -->
	        <!--  -->
			<div class="publishMes">
				<p class="notice" v-show='!userStatus'>*</p>
				<form>
					<label></label><input type="text" class="mesTitle" placeholder=""><br>
					<label></label>
					<!--  -->
					<div class="table"> 
					    <editor id="editor_id" height="500px" width="700px" :content.sync="editorText"
					            pluginsPath="../../../static/kindeditor/plugins/"
					            :loadStyleMode="false"
					            @on-content-change="onContentChange">
					    </editor>
 					 </div>
					<!--  -->
				</form>			
				<div class="pub-btn-group">
					<button @click='publish_mes'> </button>
					<button @click='loginStatus = true' v-if='!userStatus'> </button>
				</div>
			</div>		
			<!--  -->
		</div>
		<div class="footer">
			<p>&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
		</div>
	</div>
</template>
<script>
	import $ from 'jquery';
	export default {
		name:'index',
		data:function() {
			return {
				editorText: '', // 
     			editorTextCopy: '' , // content-change 
				userStatus:false,
				loginStatus:false,
				users:[],
				curUser:[],
				isfocus:false,
				judgeFocus:0
			}
		},
		mounted(){
			this.$nextTick(function(){
				var _this= this
				_this.initData()
				var curName = window.localStorage.getItem('name')
				if(curName){
					_this.userStatus = true
				}
				var storage = window.localStorage;
		    	var curName = storage.getItem('name');
		    	var _this= this;	    	
		    	if(curName){
		    		_this.userStatus = true
		    		_this.curUser.push({
		    			name:curName
		    		})
		    	}
			})
		},
		methods:{
			initData(){
				this.$http.get('../../static/userInfo.json').then((res)=>{
					this.users = res.data.users;
				})
			},
			onContentChange (val) {
		      this.editorTextCopy = val;
		    },
			publish_mes:function(e){
				var _this = this
				e.preventDefault()
				if(_this.userStatus == false){
					if(confirm('')){
						_this.loginStatus=true
					}	
				}else{
					if(_this.editorTextCopy.trim() == ''){
						alert('')
					}else{
						var pubTopic = $('.mesTitle').val();
						var date = new Date();
						var curTime = _this.$formatTime.formatTime(date)
						var storage = window.localStorage;
						storage.setItem('pubMesTopic',pubTopic);
						storage.setItem('pubMesContent',_this.editorTextCopy);
	                    storage.setItem('time',curTime);
						alert('');
					}
				}
			},
			stopProp:function(e){
				e= e || event;
				e.stopPropagation()
			},
			loginboxClick:function(){
				this.loginStatus=false
			},
	        //
	        login: function() {
	        	var _this = this
	            var username = $(".loginbox").find(".username").val(); //
	            var psw = $(".loginbox").find(".psw").val() //
	            var flag = false;
	            for (var i = 0, len = _this.users.length; i < len; i++) {
	                //
	                if (_this.users[i].username === username && _this.users[i].password === psw) {
	                	flag = true;
	                	alert(""+_this.users[i].name);
	                	$("#login")[0].reset();
	                    //
	                    _this.loginStatus = false;
	                    //
	                    _this.userStatus = true;
	                    /*localStorage*/
	                   	var storage = window.localStorage;
	                    storage.setItem('name',_this.users[i].name)
	                    storage.setItem('sex',_this.users[i].sex)
	                   	storage.setItem('id',_this.users[i].id)
	                    storage.setItem('leadership',_this.users[i].leadership)   
	                    storage.setItem('birthdate',_this.users[i].birthdate)
	                    storage.setItem('tel',_this.users[i].tel)
	                    storage.setItem('address',_this.users[i].address)
	                    break;
	                    _this.editorText = ''
	                }
	            }
	            var curName = window.localStorage.getItem('name')
	            _this.curUser.push({
	            	name:curName
	            })
	            if (!flag) {
	            	alert("");
	            	$("#login")[0].reset();
	            }
	        }
	    }
	}
</script>


<style>

@import '../css/main.css';
.table,.kindeditor{
	width: 1200px;
	height: 230px;
}
.ke-container{
	width: 1200px !important;
	height: 230px;
}
.pub-btn-group{
	margin:0;
}
.pub-btn-group button{
	position: relative;
	top:-30px;
	width: 60px;
	height: 30px;
	line-height: 30px;
	cursor: pointer;
	background: rgb(22, 123, 180);
	border: none;
	outline: none;
	margin-right: 5px;
	color: #fff;
	border-radius: 3px;
}
.pub-btn-group button:hover{
	background: rgb(0, 106, 161);
}
.hasFocus{
	border: solid 2px rgb(22,123,180) !important;
}
</style>


