<template>
	<div class="initialized">
		<div class="container" id='home'>
			<h4></h4>
			<div class="welcom_info" v-if='userStatus'>
				{{curUser[0].name}} <button @click='quit' class="quit_btn"></button>
			</div>
			<div class="nav">
				<ul>
					<li class="active"><a href="./index"></a></li>
					<li><a href="#/list"></a></li>
					<li><a href="#/publishMes"></a></li>
					<li><a href="#/permission"></a></li>
				</ul>
			</div>
			<div class="userinfo">
				<p><span><i class="icon-calendar"></i></span></p>
				<div class="userinfo-list">
					<h4 v-show='!this.userStatus'><a class="to_login" @click='$router.push("/permission")'></a></h4>
				<!-- <ul v-show='this.userStatus'>
					<li v-for='u in users'>{{u.name}}</li>
					<li v-for='u in users'>id:{{u.id}}</li>
					<li v-for='u in users'>{{u.sex}}</li>
					<li v-for='u in users'>{{u.department}}</li>
				</ul> -->
				<div v-show='this.userStatus'>
					<dl v-for='u in curUser'>
						<dd>{{u.id}}</dd>
						<dd>{{u.name}}</dd>
						<dd>{{u.sex}}</dd>
						<dd>{{u.birthdate}}</dd>
						<dd>{{u.tel}}</dd>
						<dd>{{u.address}}</dd>
						<dd>{{u.leadership}}</dd>
					</dl>
				</div>
			</div>
		</div>
		<div class="content-list">
			<p><span><i class="icon-calendar"></i></span></p>
			<ul class="index_content_list">
					<li v-for='u in users'>
						<router-link :to="'/listDetail/'+u.urlControl">
							<i class="index_words">{{u.topic}}</i> 
							<i class="index_time">{{u.time}}</i>
						</router-link>
					</li>
			</ul>
		</div>
	</div>
	<div class="footer">
		<p>&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
	</div>
</div>
</template>

<script>
	import bus from '../assets/eventBus'
	export default {
		name:'index',
		data:function() {
			return {
				userStatus:false,
				curUser:[],
				users:[],
				usersCache:[],
				msg:''
			}
		},
		methods:{
			quit(){
				window.localStorage.removeItem('id')
				window.localStorage.removeItem('name')
				window.localStorage.removeItem('sex')
				window.localStorage.removeItem('birthdate')
				window.localStorage.removeItem('tel')
				window.localStorage.removeItem('address')
				window.localStorage.removeItem('leadership')
				alert('')
				this.$router.push('/permission')
			},
			initData(){
				this.$http.get('../../static/userInfo.json').then((res)=>{
					this.usersCache=res.data.users;
					this.users=this.usersCache.slice(0,6);
				})
			}
	    },
	    mounted(){
	    	this.$nextTick(function(){
	    		var _this= this;
		    	_this.initData();
		    	var storage = window.localStorage;
		    	var curName = storage.getItem('name'),
		    		curSex = storage.getItem('sex'),
		    		curLeadership = storage.getItem('leadership'),
		    		curId = storage.getItem('id'),
		    		curBirthdate = storage.getItem('birthdate'),
		    		curTel = storage.getItem('tel'),
		    		curAddress = storage.getItem('address')
		    	if(curLeadership){
			    	if(curLeadership == 'true'){
			    		curLeadership = ''
			    	}else{
			    		curLeadership =  ''
			    	}
		    	}    	
		    	if(curName || curSex){
		    		_this.userStatus = true
		    		_this.curUser.push({
		    			name:curName,
		    			sex:curSex,
		    			leadership:curLeadership,
		    			id:curId,
		    			birthdate:curBirthdate,
		    			tel:curTel,
		    			address:curAddress
		    		})
		    	}
	    	})
	    }
	}
</script>

<style>
	@import '../css/main.css';
	@import '../lib/font-awesome.min.css';
	.initialized{
		overflow: hidden;
	}
	.to_login{
		color: red;
		cursor: pointer;
	}
	.to_login:hover{
		text-decoration: underline;
	}
	.index_content_list li{
		height: 40px;
		line-height: 40px;
		position: relative;
		border-bottom:dashed 1px blue;
		cursor: pointer;
	}
	.index_content_list li:hover{
		background: rgb(98, 170, 229);
	}
	.index_time{
		font-style: normal;
		color: #666;
		font-size: 12px;
		margin-left: 15px;
		padding-bottom: 5px;
	}
	.index_words{
		display: inline-block;
		font-style: normal;
		float: left;
		width: 850px;
		height: 40px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		padding-left: 50px;
		color: #111;
	}
</style>


